
html, body {
  padding: 0px;
  margin: 0px;
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100%;
  height: auto;
  overflow-y: scroll;
}

.home {
  min-height: calc(100% - 60px);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-bottom: 60px;
  background-color: #f0f0f0;
}
.home .bg {
  height: 480px;
  width: 100%;
  margin: 0 auto;
  background-image: url("https://assets-cdn.shimo.im/assets/images/home/index/banner_back-9564341c38.jpg");
  background-size: 1280px 580px;
  background-size: cover;
  background-position: center;
  position: absolute;
}
.home .smeditor-wrapper {
  margin-top: 18%;
}
.home .header {
  position: absolute;
  z-index: 2;
  color: #fff;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 60px;
  text-rendering: optimizeLegibility;
  font-weight: 200;
  margin-top: 8%;
  text-align: center;
  width: 100%;
}
.home .header span {
  font-size: 20px;
}
.vfooter {
  position: absolute;
  height: 50px;
  width: 100%;
  bottom: 0;
  z-index: 3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.vfooter li {
  list-style: none;
  margin: 0 5px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
}
.vfooter a {
  color: #333;
  text-decoration: none;
}
.donation {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.donation img {
  width: 30%;
}
.preview-wrapper {
  width: 70%;
  margin: 50px auto;
  border: 1px solid #bfbfbf;
  padding: 20px;
}